@-webkit-keyframes animateCircle {
    40% {
        -webkit-transform: scale(10);
                transform: scale(10);
        opacity: 1;
        fill: #dd4688;
    }
    55% {
        -webkit-transform: scale(11);
                transform: scale(11);
        opacity: 1;
        fill: #d46abf;
    }
    65% {
        -webkit-transform: scale(12);
                transform: scale(12);
        opacity: 1;
        fill: #cc8ef5;
    }
    75% {
        -webkit-transform: scale(13);
                transform: scale(13);
        opacity: 1;
        fill: transparent;
        stroke: #cc8ef5;
        stroke-width: 0.5;
    }
    85% {
        -webkit-transform: scale(17);
                transform: scale(17);
        opacity: 1;
        fill: transparent;
        stroke: #cc8ef5;
        stroke-width: 0.2;
    }
    95% {
        -webkit-transform: scale(18);
                transform: scale(18);
        opacity: 1;
        fill: transparent;
        stroke: #cc8ef5;
        stroke-width: 0.1;
    }
    100% {
        -webkit-transform: scale(19);
                transform: scale(19);
        opacity: 1;
        fill: transparent;
        stroke: #cc8ef5;
        stroke-width: 0;
    }
}

@keyframes animateCircle {
    40% {
        -webkit-transform: scale(10);
                transform: scale(10);
        opacity: 1;
        fill: #dd4688;
    }
    55% {
        -webkit-transform: scale(11);
                transform: scale(11);
        opacity: 1;
        fill: #d46abf;
    }
    65% {
        -webkit-transform: scale(12);
                transform: scale(12);
        opacity: 1;
        fill: #cc8ef5;
    }
    75% {
        -webkit-transform: scale(13);
                transform: scale(13);
        opacity: 1;
        fill: transparent;
        stroke: #cc8ef5;
        stroke-width: 0.5;
    }
    85% {
        -webkit-transform: scale(17);
                transform: scale(17);
        opacity: 1;
        fill: transparent;
        stroke: #cc8ef5;
        stroke-width: 0.2;
    }
    95% {
        -webkit-transform: scale(18);
                transform: scale(18);
        opacity: 1;
        fill: transparent;
        stroke: #cc8ef5;
        stroke-width: 0.1;
    }
    100% {
        -webkit-transform: scale(19);
                transform: scale(19);
        opacity: 1;
        fill: transparent;
        stroke: #cc8ef5;
        stroke-width: 0;
    }
}

@-webkit-keyframes animateHeart {
    0% {
        -webkit-transform: scale(0.2);
                transform: scale(0.2);
    }
    40% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes animateHeart {
    0% {
        -webkit-transform: scale(0.2);
                transform: scale(0.2);
    }
    40% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@-webkit-keyframes animateHeartOut {
    0% {
        -webkit-transform: scale(1.4);
                transform: scale(1.4);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes animateHeartOut {
    0% {
        -webkit-transform: scale(1.4);
                transform: scale(1.4);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
